.detail-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
  }
  
  .movie-poster {
    width: 300px;
    height: 450px;
    object-fit: cover;
    margin-bottom: 20px;
  }
  
  .movie-title {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .movie-genre {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .movie-rating {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .error-message {
    font-size: 24px;
    color: red;
  }
  